import React from "react";
import { NavLink, Redirect } from "react-router-dom";
// import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "react-lottie";
import Animation from "./json/sol.json";

class Step5 extends React.Component {
  componentDidMount() {
    this.startCounting();
  }
  constructor(...args) {
    super(...args);

    this.state = { counter: 0.3, elapsedTime: null };
    this.cincrement = this.cincrement.bind(this);
    this.countUp = this.countUp.bind(this);
    this.startCounting = this.startCounting.bind(this);
  }
  startCounting() {
    setInterval(this.countUp, 1000);
  }
  countUp() {
    this.setState(({ elapsedTime }) => ({ elapsedTime: elapsedTime + 1 }));
  }

  cincrement() {
    this.setState(state => ({ counter: state.counter + 1 }));
  }

  render() {
    const { elapsedTime } = this.state;
    if (elapsedTime === 11) {
      return <Redirect to="/" />;
    }
    return (
      <div>
        <div>
          {/* <ReactAudioPlayer
            src="https://upload.wikimedia.org/wikipedia/commons/7/79/Example_of_flats_in_music.ogg"
            autoPlay
            loop={true}
            volume={0.3}
          /> */}
        </div>
        <div>
          <Wizard>
            <WizardStep>
              {({ isActive, nextStep }) =>
                isActive && (
                  <div>
                    {/* <ReactAudioPlayer
                      src="https://example-files.online-convert.com/audio/ogg/example.ogg"
                      autoPlay
                      loop={true}
                    /> */}
                    <div className="buttonfooter">
                      <div className="yellowbutton" />
                    </div>
                    <div
                      className="lottie"
                      onPlayCapture
                      onClick={this.cincrement}
                    >
                      <Lottie
                        direction={1}
                        speed={this.state.counter}
                        options={{
                          animationData: Animation,
                          loop: false
                        }}
                      />
                    </div>
                  </div>
                )
              }
            </WizardStep>
            <WizardStep>
              {({ isActive }) =>
                isActive && (
                  <div>
                    <NavLink to="/" className="button btn btn-primary btn-lg">
                      Home
                    </NavLink>
                  </div>
                )
              }
            </WizardStep>
          </Wizard>
        </div>
      </div>
    );
  }
}

export default Step5;
