import React from "react";
import { NavLink, Redirect } from "react-router-dom";
// import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "lottie-react-web";
import Slider from "@material-ui/core/Slider";
import Animation from "./json/1.json";
import Animation2 from "./json/2.json";
import Animation3 from "./json/3.json";
import Animation4 from "./json/4.json";

class Step4 extends React.Component {
  componentDidMount() {}

  state = {
    slider1: 100
  };

  handleChange = name => (e, value) => {
    this.setState({
      [name]: value // --> Important bit here: This is how you set the value of sliders
    });
  };

  render() {
    const { slider1 } = this.state;

    function map_range(valor, low1, high1, low2, high2) {
      return low2 + ((high2 - low2) * (valor - low1)) / (high1 - low1);
    }
    if (slider1 === 0) {
      return <Redirect to="/cima" />;
    }
    return (
      <div>
        <div>
          {/* <ReactAudioPlayer
            src="https://upload.wikimedia.org/wikipedia/commons/7/79/Example_of_flats_in_music.ogg"
            autoPlay
            loop={true}
            volume={0.3}
          /> */}
        </div>
        <div>
          <Wizard>
            <WizardStep>
              {({ isActive, nextStep }) =>
                isActive && (
                  <div>
                    {/* <ReactAudioPlayer
                      src="https://example-files.online-convert.com/audio/ogg/example.ogg"
                      autoPlay
                      loop={true}
                    /> */}
                    <div className="buttonfooter">
                      <div className="yellowbutton" onClick={nextStep} />
                    </div>
                    <div style={{ padding: 20 }}>
                      <Slider
                        value={slider1}
                        aria-labelledby="label"
                        onChange={this.handleChange("slider1")}
                        max={100}
                        min={0}
                        //defaultValue={1}
                      />
                    </div>
                    <div className="lottie" style={{ opacity: 1 }}>
                      <Lottie
                        direction={1}
                        speed={1}
                        options={{
                          animationData: Animation,
                          loop: true
                        }}
                      />
                    </div>
                    <div
                      className="lottie"
                      style={{ opacity: map_range(slider1, 0, 33, 0, 1) }}
                    >
                      <Lottie
                        direction={1}
                        speed={1}
                        options={{
                          animationData: Animation2,
                          loop: true
                        }}
                      />
                    </div>
                    <div
                      className="lottie"
                      style={{ opacity: map_range(slider1, 33, 66, 0, 1) }}
                    >
                      <Lottie
                        direction={1}
                        speed={1}
                        options={{
                          animationData: Animation3,
                          loop: true
                        }}
                      />
                    </div>
                    <div
                      className="lottie"
                      style={{ opacity: map_range(slider1, 66, 100, 0, 1) }}
                    >
                      <Lottie
                        direction={1}
                        speed={1}
                        options={{
                          animationData: Animation4,
                          loop: true
                        }}
                      />
                    </div>
                  </div>
                )
              }
            </WizardStep>

            <WizardStep>
              {({ isActive, nextStep }) =>
                isActive && (
                  <div onClick={nextStep}>
                    {/* <ReactAudioPlayer
                      src="https://upload.wikimedia.org/wikipedia/commons/c/c8/Example.ogg"
                      autoPlay={true}
                      loop={true}
                    /> */}
                    <div className="lottie">
                      <Lottie
                        direction={1}
                        speed={1}
                        options={{
                          animationData: Animation,
                          loop: true
                        }}
                      />
                    </div>
                  </div>
                )
              }
            </WizardStep>

            <WizardStep>
              {({ isActive }) =>
                isActive && (
                  <div>
                    <NavLink
                      to="/cima"
                      className="button btn btn-primary btn-lg"
                    >
                      cima
                    </NavLink>
                  </div>
                )
              }
            </WizardStep>
          </Wizard>
        </div>
      </div>
    );
  }
}

export default Step4;
