import React from "react";
import { Stage, Layer, Text } from "react-konva";
import { NavLink } from "react-router-dom";
import Lottie from "lottie-react-web";
import Animation1 from "./json/1.json";

class Step3 extends React.Component {
  componentDidMount() {}
  state = {
    isDragging: false,
    x: 50,
    y: 50
  };
  render() {
    return (
      <div>
        <div>
          <NavLink to="/silencio" className="button btn btn-primary btn-lg">
            Next
          </NavLink>
        </div>

        <div className="lottie" style={{ position: "fixed" }}>
          <Lottie
            direction={1}
            speed={1}
            options={{
              animationData: Animation1,
              loop: true
            }}
          />
        </div>
        <div>
          <Stage width={window.innerWidth} height={window.innerHeight}>
            <Layer>
              <Text
                text="Draggable Text"
                x={this.state.x}
                y={this.state.y}
                draggable
                fill={this.state.isDragging ? "green" : "black"}
                onDragStart={() => {
                  this.setState({
                    isDragging: true
                  });
                }}
                onDragEnd={e => {
                  this.setState({
                    isDragging: false,
                    x: e.target.x(),
                    y: e.target.y()
                  });
                }}
              />
            </Layer>
          </Stage>
        </div>
      </div>
    );
  }
}

export default Step3;
